import ButtonBox from "components/ButtonBox/ButtonBox";
import { Column, EditableComponentRef } from "components/EditableTableV2/EditableTableV2";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import FileUploadBox from "components/FileUploadBox/FileUploadBox";
import IconButton from "components/IconButton/IconButton";
import Info from "components/Info/Info";
import MenuBox from "components/MenuBox/MenuBox";
import ReadOnlyTable from "components/ReadOnlyTable/ReadOnlyTable";
import Section from "components/Section/Section";
import Text from "components/Text/Text";
import { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import FileUploadService from "services/FileUploadService";
import LegalService from "services/transaction/DDE/LegalService";
import { formatDate, parseStandardAPIErrorMessage } from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  DOCUMENT_CATEGORY,
  DOCUMENT_SECTION,
  LOAN_SECTION_KEY,
  VARIANTS,
} from "utils/Constant";
import { isLegalSectionAccessDisabled } from "../Helper";

interface IPageObj {
  showInfo: boolean;
  stageUid: string;
}

const initialPageOj: IPageObj = {
  showInfo: true,
  stageUid: "",
};

interface IRowData {
  uid: string;
  createdByUserName: string;
  updatedAt: string;
  documentSections: {
    uid: string;
    fileName: string;
    fileType: string;
    thumbUrl: string;
    url: string;
  }[];
}

const PageDocumentExchange = () => {
  const params: { loanUid?: string; stageUid?: string } = useParams();

  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
  */

  const [pageObj, setPageObj] = useState<IPageObj>({ ...initialPageOj });
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [errorMessage, setErrorMessage] = useState<any>("");

  // ref
  const tableRef = useRef<EditableComponentRef>(null);
  const actionMenuRef = useRef<any>(null);

  // --------------------------- global variables ---------------------- //
  const actionMenuModel: any[] = [
    {
      items: [],
    },
  ];

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      stageUid: params?.stageUid ?? "",
    }));
  }, []);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */
  const onCloseInfo = () => {
    setPageObj((prevPageObj) => ({ ...prevPageObj, showInfo: false }));
  };

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */
  const onDeleteRow = async (rowData: any) => {
    console.log("delete row data");
    try {
      const payload = enrichForUpdate(rowData);
      const res = await LegalService.updateDocumentExchange(payload);
      tableRef.current?.search();
    } catch (error) {
      console.log(error);
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }
  };

  const onAddDocument = async (e: any) => {
    const file = e.files[0];
    // Generate a unique object name
    const timestamp = new Date().getTime();
    const randomString = Math.random().toString(36).substring(2, 8);
    const fileNameSplit = file?.name?.split(".");
    let fileNameWithoutExtension = "";
    if (fileNameSplit?.length > 1) {
      for (let i = 0; i < fileNameSplit.length - 1; i++) {
        if (i === fileNameSplit.length - 1) {
          break;
        }
        fileNameWithoutExtension += fileNameSplit[i];
      }
      const objectName = `${fileNameWithoutExtension}_${timestamp}_${randomString}.${
        fileNameSplit[fileNameSplit.length - 1]
      }`;

      try {
        const res = await FileUploadService.saveFile(file, objectName);
        const payload = enrichForCreate(res.data);

        const res2 = await LegalService.updateDocumentExchange(payload);
        tableRef.current?.search();
      } catch (error) {
        console.log(error);
        setErrorMessage(parseStandardAPIErrorMessage(error));
      }
    }
  };

  const onDownloadDocument = async (rowData: IRowData) => {
    const fileUrl = rowData?.documentSections?.[0]?.url;
    const fileName = rowData?.documentSections?.[0]?.fileName;

    // getSignedUrl
    // downloadFileFromUrl
    try {
      const res = await FileUploadService.getSignedURL(fileUrl);

      fetch(res.data?.SignedUrl)
        .then((response) => {
          if (!response.ok) {
            throw new Error("Network response was not ok: " + response.status);
          }
          return response.blob();
        })
        .then((blob) => {
          const blobUrl = URL.createObjectURL(blob);
          const downloadLink = document.createElement("a");
          downloadLink.href = blobUrl;
          downloadLink.download = fileName;
          document.body.appendChild(downloadLink);
          downloadLink.click();
          document.body.removeChild(downloadLink);
          URL.revokeObjectURL(blobUrl);
        })
        .catch((error) => {
          console.error("Error fetching the file:", error);
        });
    } catch (error) {
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }

    // Fetch the file as a Blob
  };

  const onClickActionMenu = (e: any, rowData: any) => {
    // dynamic menu options array
    const dynamicActionMenuOptions: any[] = [];

    dynamicActionMenuOptions.push(
      // --------------------Stop Delegation ------------------------  //
      {
        label: "Remove",
        //icon: //JSX element,
        className: "px-1.5 text-xs text-primary-labelColor h-6.5 hover:bg-primary-sapphireBlue-100",
        command: () => {
          onDeleteRow(rowData);
        },
      }
    );

    // common menu options
    const menu: any[] = [...dynamicActionMenuOptions];

    actionMenuModel[0].items = [...menu];
    actionMenuRef?.current?.show(e);
  };

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  const enrichForCreate = (signedData: any) => {
    const obj = {
      docType: {
        uid: "a1f9c167-ecbb-461d-9b47-7b1efebc6a19",
      },
      loanSectionKey: LOAN_SECTION_KEY.DOCUMENT_EXCHANGE,
      docCategory: DOCUMENT_CATEGORY.DOCUMENT_CATEGORY_EXCHANGE_DOCUMENTS,
      stage: {
        uid: pageObj.stageUid,
      },
      loan: {
        uid: params?.loanUid,
      },
      documentSections: [
        {
          section: DOCUMENT_SECTION.DOCUMENT_SECTIONS_FILE_1,
          fileName: signedData?.fileName,
          fileType: signedData?.contentType,
          thumbUrl: signedData?.thumbUrl,
          url: signedData?.url,
        },
      ],
    };
    return obj;
  };

  const enrichForUpdate = (data: IRowData) => {
    const obj = {
      uid: data?.uid,
      docType: {
        uid: "a1f9c167-ecbb-461d-9b47-7b1efebc6a19",
      },
      loanSectionKey: LOAN_SECTION_KEY.DOCUMENT_EXCHANGE,
      docCategory: DOCUMENT_CATEGORY.DOCUMENT_CATEGORY_EXCHANGE_DOCUMENTS,
      stage: {
        uid: pageObj.stageUid,
      },
      loan: {
        uid: params?.loanUid,
      },
      documentSections: [
        {
          uid: data?.documentSections?.[0]?.uid,
          section: DOCUMENT_SECTION.DOCUMENT_SECTIONS_FILE_1,
          fileName: data?.documentSections?.[0]?.fileName,
          fileType: data?.documentSections?.[0]?.fileType,
          thumbUrl: data?.documentSections?.[0]?.thumbUrl,
          url: data?.documentSections?.[0]?.url,
          isActive: false,
        },
      ],
      isActive: false,
    };
    return obj;
  };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */

  const renderUser = (rowData: IRowData) => {
    return <span>{rowData?.createdByUserName ?? ""}</span>;
  };
  const renderFileName = (rowData: IRowData) => {
    return <span>{rowData?.documentSections?.[0].fileName}</span>;
  };
  const renderFileType = (rowData: IRowData) => {
    return <span>{rowData?.documentSections?.[0].fileType ?? ""}</span>;
  };
  const renderUpdatedOn = (rowData: IRowData) => {
    return <span>{formatDate(rowData?.updatedAt) ?? ""}</span>;
  };
  const renderAction = (rowData: IRowData) => {
    return (
      <div className="flex gap-2 justify-center items-center">
        <IconButton
          onClick={() => onDownloadDocument(rowData)}
          hideDefaultIconClassName
          buttonType={"DOWNLOAD"}
          tooltipOptions={{
            position: "top",
            className: "text-xs font-Roboto bg-black text-white !p-1 rounded-md ",
            showDelay: "1000,",
            hideDelay: "300",
          }}
        />
        <ButtonBox
          buttonType={BUTTON_TYPE.MENU}
          onClick={(e: any) => onClickActionMenu(e, rowData)}
        />
      </div>
    );
  };
  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */
  return (
    <div className="grid gap-5 mb-5">
      {pageObj?.showInfo ? (
        <Info description="description.legal.info" onClose={onCloseInfo} />
      ) : null}
      <Section title="heading.legal.legalDocumentExchange">
        <>
          <div className="flex justify-end">
            <div className=" mr-3 self-center">
              <FileUploadBox
                label={`Add`}
                onSelect={onAddDocument}
                variant={VARIANTS.PRIMARY}
              />
            </div>
          </div>
          {params?.loanUid && pageObj?.stageUid ? (
            <ReadOnlyTable
              ref={tableRef}
              standardDataFormatPromise={LegalService.getDocumentExchangeList}
              search={`&loanUid=${params.loanUid}&stageUid=${pageObj.stageUid}&loanSectionKey=${LOAN_SECTION_KEY.DOCUMENT_EXCHANGE}&isActive=true`}
              dataKey="uid"
            >
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={"User"}
                body={renderUser}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={"File Name"}
                body={renderFileName}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={"File Type"}
                body={renderFileType}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={"Updated On"}
                body={renderUpdatedOn}
              />
              <Column
                className="text-primary-ptext-1000 text-center text-xs p-1 h-12 px-6 action-content"
                header={"Actions"}
                body={renderAction}
              />
            </ReadOnlyTable>
          ) : null}
        </>
      </Section>

      <MenuBox
        ref={actionMenuRef}
        model={actionMenuModel}
        variant="SECONDARY"
        buttonClassName="!bg-transparent"
        className="width-full-menubox bg-white  w-max p-1.5 rounded-md mt-0.5 border shadow-autoCompletePanel"
      />

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
};

export default PageDocumentExchange;
